body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html,
body{
background-color: #ECE5DD;

}

.App{
    display: flex;
    flex-wrap:wrap;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

.logo-container{
    width: 100%;
    display: flex;
    flex-direction:row;
    align-items: center;
    justify-content: center;
}


.logo{
    height: 200px;
}

.principal-container{
    min-width:550px;
    height: 500px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
button{
  width: 250px;
  height: 100px;
  font-family: Roboto, sans-serif;
  font-weight: 900;
  font-size: 2rem;
  color: #ffffff; 
  border-radius: 12px;
  margin: 5px;
  cursor:pointer;
}

button:hover{
background-color: #075e54;
}

.click-button{
background-color:#25d366 ;
}


.restart-button{
  background-color: #128c7e;
}
.counter{
    min-width: 300px;
    height: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #dcf8c6;
    border-radius: 15px;
    border: 1px solid gray;
    box-shadow: 2px 2px 20px 5px rgba(7, 94, 84, 0.2);
    color:#000;
    font-family: roboto , sans-serif;
    font-weight: 900;
    font-size: 120px;
    margin-bottom: 10px;
    padding: 25px;
}


